import React, {useState} from "react";
import {ActionBtn} from "./button";
import {UserDisplay,UserPicture} from "../profile";




export function ParentTweet(props) {
    const {tweet} = props;
    return tweet.parent ?
        <Tweet hideActions isReTweet retweeter={props.retweeter} className={' '} tweet={tweet.parent}/> : null
}


export function Tweet(props) {
    const {tweet, didRetweet, hideActions, isReTweet, retweeter} = props;

    const [actionTweet, setActionTweet] = useState(props.tweet ? props.tweet : null);
    let className = props.className ? props.className : 'col-10 mx-auto col-md-6';
    className = isReTweet === true ? `${className} p-2 border rounded` : className;
    const handlePerformAction = (newActionTweet, status) => {
        if (status === 200) {
            setActionTweet(newActionTweet);
        } else if (status === 201) {
            //let 'em known tweet created
            if (didRetweet) {
                didRetweet(newActionTweet)
            }
        }
    };

    const path = window.location.pathname;
    const idRegex = /(?<id>\d+)/;
    const match = path.match(idRegex);
    const urlTweetId = match ? match.groups.id : -1;

    const isDetail = `${tweet.id}` === `${urlTweetId}`;
    const handleLink = (event) => {
        event.preventDefault();
        window.location.href = `/${tweet.id}`
    };
    const user = tweet.user;
    console.log(user.username);
    if (user && user.username)
        console.log(user.username);
// {(`${tweet.user}` && `${tweet.user}`)}

    return <div className={className}>
        {isReTweet === true &&
        <div className='ml-2 mb-2 '><span className='small text-muted'>ReTweet via  <UserDisplay user={retweeter}/></span></div>}
        <div className='d-flex'>

            <div className='col-1 '>
                <UserPicture user={tweet.user}/>
            </div>
            <div className='col-11'>
                <div>

                    <p><UserDisplay includeFullName user={tweet.user}/></p>
                    <p>{tweet.content}</p>

                    <ParentTweet tweet={tweet} retweeter={tweet.user}/>
                </div>
                <div className="btn btn-group-justified px-0">
                    {(actionTweet && hideActions !== true) && <React.Fragment>
                        <ActionBtn tweet={actionTweet} didPerformAction={handlePerformAction}
                                   action={{type: "like", display: "Likes"}}/>
                        <ActionBtn tweet={actionTweet} didPerformAction={handlePerformAction}
                                   action={{type: "unlike", display: "UnLike"}}/>
                        <ActionBtn tweet={actionTweet} didPerformAction={handlePerformAction}
                                   action={{type: "retweet", display: "ReTweet"}}/></React.Fragment>}
                    {isDetail !== false ? null :
                        <button className='btn btn-outline-primary btn-sm' onClick={handleLink}>
                            View
                        </button>}
                </div>
            </div>
        </div>
    </div>


}
